*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: 20px;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body{
  width: 100%;
  height: 100%;
  padding : 0 !important;
}
.home-container , .my-container{
  display: flex;
  width: 100%;
}
:root{
  --color1: #0d6efd;
  --color2: #ffc107;
  --color3: #2bf65a;
  --color4: #ffffff; 
  --color5: #000000;
  --color6: #060606;
  --color7: #343434;
  --color8: #ff2e69;
  --color9: #07daff;
  --color10: #f28634;
  --color11: #ef24ef;
  --color12: #0249b4;
  --color13: #008a00;
  --color14: #b1b1b1;
  --color15: #08b9ff;
}
.text-style-change{
  font-size: 3rem;
  font-family: 'Bebas Neue', cursive;
}
/* ------------------------ NavBar -------------------------*/
.container-part-one{
  position: fixed; 
  display: block;
  width: 20%;
  height : 100vh;
  background-color: var(--color1);
  overflow: hidden;
}
.container-part-two{
  width: 100%;
  margin-left: 20%;
  padding: 0rem 1rem ; 
  background-color: yellow;
}
.flex-class{
  display: flex;
  justify-content: flex-start;
  gap: 1rem;
  align-items: center;
}
.image-class{
  width: 2rem;
  border-radius: 50%;
}
.logo-name{
  font-size: 1.5rem;
  font-weight: bold;
  font-family: cursive;
  color: var(--color6);
}
.dropdown-menu{
  padding : 0.5rem;
}
.dropdown-item:hover{
  background-color: #a9b9ff;
}
.navbar-opt{
  text-align: center;
  padding: 0rem;
}
.navbar-opt li{
  padding-top: 1rem;
  list-style-type: none;
  display: block;
  font-weight: bold;
  font-size: 1.125rem;
}
.flex-center{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.flex-class2{
  display: flex;
  gap: 1rem;
  align-items: center;
}
.verdana-family{
  font-family: verdana;
}
.icon-container{
  width: 2rem;
  height: 2rem;
  padding : 0.125rem;
  border-radius: 50%;
}
.icons-opt{
  font-size: 1.25rem;
  cursor: pointer;
}
.change-on-hover .change-on-hover2{
  transition: all 0.25s linear;
}
.icons-color{
  background-color: var(--color7);
  transition: all 0.25s linear;
}
.icon-color1{
  color: var(--color2);
}
.change-on-hover:hover{
  background-color: var(--color12);
  color: var(--color4);
  border-radius: 1rem;
}
.change-on-hover:hover .icon-color1{
  background-color: var(--color2); 
  color: var(--color7);
}
.icon-color2{
  color: var(--color11);
}
.change-on-hover2:hover .icon-color2{
  background-color: var(--color11); 
  color: var(--color7);
}
.icon-color3{
  color: var(--color8);
}
.change-on-hover3:hover .icon-color3{
  background-color: var(--color8);
  color: var(--color7);
}
.icon-color5{
  color: var(--color3);
}
.change-on-hover5:hover .icon-color5{
  background-color: var(--color3);
  color: var(--color7);
}
.icon-color6{
  color: var(--color9);
}
.change-on-hover6:hover .icon-color6{
  background-color: var(--color9);
  color: var(--color7);
}
.icon-color7{
  color: var(--color1);
}

.gayab-class{
  display: none;
}
/* ----------------------- Home-Page ----------------------------- */
.post-top{
  display:flex;
  padding: 0.5rem;
  justify-content: space-between;
}
.post-top2{
  display : flex;
  gap : 0.5rem;
}
.image-class2{
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
}
.border-bottom-class{
  border-bottom: 0.125rem solid rgba(63, 63, 63, 0.727);
}
.card-body{
  padding-top: 0rem !important;
}
.some-text{
  color: var(--color15);
  text-align: left !important;
  font-family: 'Lucida Sans', 'Lucida Sans Regular';
}
.read-more{
  display : flex;
  justify-content: space-between;
  align-items: center;
}
.card-img-top{
  height: 11rem;
}

.profile-image{
  width : 10rem;
  height : 12rem;
}
.margin-left{
  margin-left: 20%;
}
.fixed-width{
  width: 20rem;
}

.Search-part-two{
  margin-left: 20%;
  padding : 0rem 1rem 1rem 1rem;
}
.Search-bar{
  display: flex;
  justify-content: space-between;
  text-align: center;
}
.right-part{
  display: flex;
  gap: 1rem;
}
.profile-image-class{
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
}
.icon-options{
  display: flex;
  gap : 1rem;
  padding: 0.5rem 1rem;
}
.like-icon{
  color : #000000;
}
.like-icon:active{
  color: #ff0000;
}
.red-color{
  color : rgb(255, 0, 0);
}
.red-color:active{
  color : black;
}

/* SPINNER CLASS */
.absolute-center{
  display: flex;
  justify-content: center;
  align-items: center;
}
.black-color{
  text-decoration: none;
  color : black;
}
.little-text{
  height : 1.5rem;
  overflow: hidden;
}
/* ----------------------------- SEARCH PAGE ---------------------------------- */
.fixed-height-100{
  min-height: 100vh;
}
.flex-class-search-form{
  display: flex;
  justify-content: center;
  align-items: center;
  gap : 0.25rem;
}
.control-width{
  width: 30rem;
}
.hover-change:hover{
  color: var(--color13);
  background-color: var(--color4);
}
.back-design{
  background-color: #323232;
  border-top-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
}

/* ------------------------ SEARCHED PROFILE PAGE --------------------------------*/
.Container-part-two{
  margin-left: 20%;
  padding: 0rem 1rem;
  background-color: var(--color6);
}
.virtual-height{
  height: 4.5rem;
  margin: 0rem 8rem;
  border-top-right-radius: 1rem;
  border-top-left-radius: 1rem;
}
.virtual-down{
  margin : 0rem 8rem;
  padding : 1rem 0.5rem 2rem 0.5rem ;
  color: var(--color4);
  border-bottom-right-radius: 1rem;
  border-bottom-left-radius: 1rem;
  background-color: var(--color6);
}
.absolute-image{
  width: 6rem;
  aspect-ratio: 1/1;
  border-radius: 50%;
  border: 0.125rem solid var(--color5);
  margin-top: -4rem;
}
.font-size-adjust{
  font-size: 1.5rem;
}
.font-size-adjust2{
  font-size: 1.25rem;
}
.border-radius{
  border-radius: 2rem;
  padding: 0.125rem 1.25rem !important; 
  font-weight: bold;
}
.fix-icon{
  font-size: 1.25rem;
}
.follow-count-section{
  display : flex;
  justify-content: center;
  gap: 2.5rem;
}
.desc{
  font-family:'Lucida Sans';
}
.font-family-change{
  font-family: 'Tilt Prism', cursive;
  letter-spacing: 0.25rem;
  border-top: 0.125rem solid var(--color1);
  border-image: linear-gradient(to right , var(--color1) , var(--color4))1;
}
.flex-class-card{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  flex-wrap: wrap;
}
.card-width{
  width: 15rem;
  border: none !important;
  border-bottom-left-radius: 2rem !important; 
  border-bottom-right-radius: 2rem !important; 
}
.border-radius2{
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem; 
  background-color: var(--color6);
}
/* ---------------------- LOGIN PAGE ----------------------------- */
.flex-class3{
  display: flex;
  align-items: center;
  justify-content: center;
  gap : 2rem;
  margin : 0rem 10rem; 
  background-color: var(--color7);
  border-top-right-radius: 1rem;
  border-top-left-radius: 1rem;
}
.input-style , .input-style:focus{
  outline: 0;
  color: var(--color4);
  padding-bottom: 0.125rem;
  border-width: 0;
  border-bottom: 0.125rem solid var(--color4);
  background-color: var(--color7);
}
.input-style:focus{
  border-color: var(--color1);
}
.input-style::-webkit-input-placeholder {
  color: var(--color14);
}
.radius-border{
  border-radius: 1rem;
}
.login-image{
  width : 20rem;
}
.set-margin-se{
  background-color: var(--color7);
  margin: 0rem 10rem; 
  border-bottom-right-radius: 1rem;
  border-bottom-left-radius: 1rem;
}
.opacity-set{
  opacity: 1;
}
.remove-underline{
  text-decoration: none;
}
/* ---------------------- Forgot Password --------------------------- */
.text-align-left{
  text-align: left !important;
}
.set-margin{
  margin: 0rem 20rem;
  border-radius: 1.5rem;
}
/* ------------------------- Register Page ---------------------------- */
.flex-class4{
  justify-content: space-evenly;
  gap: 0rem;
  padding: 0rem !important;
  border-radius: 1rem;
}
.image-upload>input{
  display: none;
}
.regimage-part-container{
  width: 30%;
  height: 90%;
  background-color: var(--color1);
}
.register-image{
  width: 38%;
  object-fit: cover;
}
.width-adjust{
  width: 100%;
}
/* ---------------------- New - Post ---------------------------- */
.set-se-margin{
  margin-left: 8rem;
  margin-right: 8rem;
  border-radius: 1.5rem;
}
.text-left{
  text-align: left !important;
}
.text-right{
  text-align: right !important;
}
.fix-input-back{
  color : var(--color15);
  background-color: var(--color5);
  border-radius: 0.5rem;
}
.fix-input-back::placeholder{
  color : var(--color4);
}
.fix-input-back:focus{
  color: var(--color9);
}
/* ----------------------------- Tweet Detail ------------------------------ */
.black-background{
  background-color: var(--color7);
  padding: 0.125rem;
}
.comment-image-class{
  width : 4rem;
  border-radius: 50%;
}
.no-gap{
  gap: 0.125rem;
}
.flex-spaceBetween{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
/* ------------------- About Us Page -------------------- */
.about-logo-image{
  width : 12rem;
  border-radius: 50%;
  aspect-ratio: 1/1;
}
/* -------------------- Edit Profile ------------------------ */
.image-change-container>input{
  display: none;
}
.pointer{
  cursor: pointer;
  margin-left: -1rem !important;
}
.fixed-width2{
  width : 15rem;
}
.flex-class5{
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
.edit-input , .edit-input:focus{
  outline: 0;
  color: var(--color4);
  padding-bottom: 0.125rem;
  border-width: 0;
  border-bottom: 0.125rem solid var(--color4);
  background-color: var(--color5);
}
.edit-input:focus{
  border-color: var(--color1);
}
.edit-input::-webkit-input-placeholder {
  color: var(--color5);
}

/* ----------------------------------- RESPONSIVENESS -------------------------------- */

@media screen and (max-width: 1220px){
  .set-se-margin{
    margin-left: 6rem;
    margin-right: 6rem;    
  }
  .set-margin{
    margin: 0rem 15rem;
  }
  .flex-class3 .set-margin-se{
    margin : 0rem 6rem ;
  }
  .login-image{
    width: 15rem;
  }
}
@media screen and (max-width: 1115px){
  .login-image{
    width: 12rem;
  }
}
@media screen and (max-width: 1100px){
  .container-part-one{
    display: none;
  }
  .gayab-class{
    display : block;
    position: absolute;
    top : 1.25rem;
    left : 0.5rem;
  }
  .container-part-two , .Search-part-two , .Container-part-two , .margin-left{
    margin-left: 0rem; 
  }
} 
@media screen and (max-width: 1030px){
  .login-image{
    width: 10rem;
  }
}
@media screen and (max-width: 995px){
  .flex-class3 , .set-margin-se{
    margin : 0rem 4rem;
  }
}
@media screen and (max-width: 970px){
  .set-margin{
    margin : 0rem 10rem;
  }
}
@media screen and (max-width: 900px){
  .set-se-margin{
    margin-left: 4rem;
    margin-right: 4rem;    
  }
}
@media screen and (max-width: 870px) {
  .flex-class5{
    flex-direction: column;
  }
}
@media screen and (max-width: 825px){
  .set-se-margin{
    margin-left: 2rem;
    margin-right: 2rem;    
  }
}
@media screen and (max-width: 800px){
  .follow-count-section2{
    gap: 1rem !important;
  } 
}
@media screen and (max-width: 770px){
  .set-margin{
    margin : 0rem 6rem;
  }
}
@media screen and (max-width: 750px){
  .set-se-margin{
    margin-left: 0.125rem;
    margin-right: 0.125rem;    
  }
  .set-margin-se , .flex-class3{
    margin : 0rem 2rem; 
  }
}
@media screen and (max-width: 720px){
  .control-width{
    width: 20rem;
  }
}
@media screen and (max-width: 730px){
  .virtual-down , .virtual-height{
    margin : 0rem 6rem;
  }
}
@media screen and (max-width: 680px){
  .set-margin-se , .flex-class3{
    margin : 0rem 0.5rem; 
  }
}
@media screen and (max-width: 640px){
  .virtual-down , .virtual-height{
    margin : 0rem 4rem;
  }
}
@media screen and (max-width: 605px){
  .flex-class3{
    flex-direction: column;
    gap: 0rem;
    margin: 0rem 1rem;
  }
  .set-margin{
    margin: 0rem 2rem;
  }
  .set-margin-se{
    margin: 0rem 1rem;
  }
  .fix-padding{
    padding-bottom: 0rem !important;
  }
  .big-on-small{
    font-size: 1.5rem;
  }
}
@media screen and (max-width:600px){
  .register-image{
    display: none;
  }
}
@media screen and (max-width: 560px){
  .virtual-down , .virtual-height{
    margin : 0rem 2rem;
  }
}
@media screen and (max-width: 525px){
  .forgot-big{
    font-size: 1.25rem;
  }
}
@media screen and (max-width: 500px){
  *{
    font-size: 18px;
  }
  .gayab-on-440{
    display: none;
  }
}
@media screen and (max-width: 440px){
  *{
    font-size: 16px;
  }
  .fixing-width{
    width: 18rem;
  }
  .margin-on-bigger{
    margin: 1rem 0.125rem !important;
  }
}
@media screen and (max-width: 400px){
  *{
    font-size: 14px;
  }
  .margin-on-small{
    margin: 2rem 0.125rem !important; 
  }
  .make-big{
    font-size: 1.5rem;
  }
}
@media screen and (max-width: 350px){
  *{
    font-size: 12px;
  }
}
@media screen and (max-width: 330px){
  .fixing-width{
    width: 15rem;
  }
}
@media screen and (max-width: 300px){
  .input-style{
    width: 15rem;
  }
  .virtual-down , .virtual-height{
    margin : 0rem 0rem !important;
  }
  .follow-count-section2{
    gap: 0.75rem !important;
  }
  .virtual-height{
    border-top-right-radius: 0.5rem;
    border-top-left-radius: 0.5rem;
  }
  .fixed-width2{
    width: 12rem;
  }
}
@media screen and (max-width: 290px){
  .search-page-head{
    margin-left: 2rem ;
    font-size: 2.75rem;
  }
  .card{
    width : 15rem !important;
  }
  .about-page-head{
    margin-left: 1.5rem;
  }
  .control-width{
    width: 15rem;
  }
  .set-left-margin{
    margin-left: 2rem;
  }
  .set-se-margin{
    margin-left: 0rem;
    margin-right: 0rem;    
  }
  .set-margin-right{
    margin-right: 2rem;
  }
  .width-adjust{
    width : 50%;
  }
  .flex-class3, .set-margin-se{
    margin: 0rem 0.125rem;
  }
}
@media screen and (max-width: 270px){
  .fixing-width{
    width: 12rem;
  }
}
@media screen and (max-width: 265px){
  .comp-page-head{
    font-size: 2.5rem !important;
    margin-left: 1.5rem;
    padding-top: 1.5rem !important;
  }
  .follow-count-section{
    gap: 1rem;
  }
}
@media screen and (max-width: 240px){
  .follow-count-section2{
    flex-direction: column !important;
  }
  .control-width{
    width: 12rem;
  }
  .right-part{
    justify-content: center;
    margin-bottom: 0.5rem;
  }
  .Search-bar{
    flex-direction: column;
    margin-bottom: 1rem;
  }
  .page-head{
    font-size: 2rem !important;
    margin-left: 1.5rem;
    padding-top: 1.5rem !important;
  }
}
@media screen and (max-width: 225px) {
  .m-l-225{
    margin-left: 1.5rem;
  }
  .gayab-on-small{
    display: none;
  }
}